.container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.switcher-btn {
  color: #000;
  width: 150px;
  height: 50px;
  cursor: pointer;
  background-color: #e7e7e7;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 15px 32px;
  font-size: 20px;
  transition-duration: .4s;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

.switcher-btn[disabled] {
  opacity: .5;
  cursor: not-allowed;
}

.switcher-btn:hover {
  color: #fff;
  background-color: #000;
  box-shadow: 0 12px 16px rgba(0, 0, 0, .24), 0 17px 50px rgba(0, 0, 0, .19);
}

